.tareas {
  width: 75%;
}

.alert {
  background-color: gray;
  padding: 2rem;
  border-radius: 1rem;
}
.alert.alert-primary {
  background-color: var(--blue-100);
  color: var(--primary);
}

.alert.alert-secondary {
  background-color: var(--gray-200);
  color: var(--secondary);
}

.alert.alert-success {
  background-color: var(--green-100);
  color: var(--success);
}

.alert.alert-info {
  background-color: var(--info);
  color: var(--info);
}

.alert.alert-warning {
  background-color: var(--warning);
  color: var(--warning);
}

.alert.alert-danger {
  background-color: var(--red-100);
  color: var(--danger);
}

.alert.alert-light {
  background-color: var(--light);
  color: var(--light);
}

.alert.alert-dark {
  background-color: var(--dark);
  color: var(--dark);
}
.alert.alert-especial {
  background-color: var(--especial2);
  color: var(--white);
}

button {
  border: none;
  border-radius: 1rem;
  background-color: var(--gray-600);
  box-shadow: 2px 2px 2px 1px var(--gray-200);
  box-sizing: content-box;
  color: var(--white);
  display: inline-block;
  font-family: var(--font-family);
  font-size: 18px;
  margin: 1rem;
  padding: 6px 10px;
  text-decoration: none;
  text-align: center;
}

button:hover {
  background-color: var(--especial);
  box-shadow: 2px 2px 2px 1px var(--gray-400);
  cursor: pointer;
}

.btn.btn-primary {
  background-color: var(--primary);
}

.btn.btn-secondary {
  background-color: var(--secondary);
  color: var(--white);
}

.btn.btn-success {
  background-color: var(--success);
  color: var(--white);
}

.btn.btn-info {
  background-color: var(--info);
  color: var(--white);
}

.btn.btn-warning {
  background-color: var(--warning);
  color: var(--white);
}

.btn.btn-danger {
  background-color: var(--danger);
  color: var(--white);
}

.btn.btn-light {
  background-color: var(--light);
  color: var(--white);
}

.card-title {
  font-family: var(--font-family);
  font-weight: normal;
  text-align: center;
  margin-bottom: 20px;
}
.card {
  background-color: #fff;
  background-clip: border-box;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
  display: flex;
  flex-direction: column;
  margin: 3px 0;
  padding: 5%;
  position: relative;
  text-align: center;
  word-wrap: break-word;
  border-radius: 1rem;
}

form p {
  font-size: 1.3rem;
  margin: 0;
}

legend {
  font-size: 2.4rem;
  color: #333333;
}

label {
  display: block;
  font-weight: 700;
  text-transform: uppercase;
}

input:not([type="submit"]),
textarea,
select {
  padding: 1rem;
  display: block;
  background-color: #e1e1e1;
  margin-bottom: 2rem;
  border: none;
  border-radius: 1rem;
}

input[type="radio"] {
  width: auto;
}

select {
  -webkit-appearance: none;
  appearance: none;
}

textarea {
  height: 20rem;
  resize: none;
}

.modal--overlay {
  background-color: rgba(216, 216, 216, 0.5);
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  display: grid;
  align-items: center;
  justify-content: center;
}

.modal {
  background-color: #ffffff;
  overflow-y: scroll;
  text-align: center;
  border-radius: 1rem;
  padding: 5rem;
  max-height: 80%;
  max-width: 80%;
  margin: 5%;
  box-sizing: content-box;
  position: relative;

  display: grid;
  align-items: center;
  justify-content: center;
}

.modal--close-btn {
  background-image: url(/static/media/close.fb5e5666.svg);
  background-repeat: no-repeat;
  background-size: 20px 20px;
  background-position: center;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 1rem;
  right: 1rem;
  right: -10;
  cursor: pointer;
  z-index: 2;

  background-color: rgba(1, 1, 1, 0);
  box-shadow: none;
}
.modal--close-btn:hover {
  background-color: rgba(1, 1, 1, 0);
  box-shadow: none;
}

.progressbar {
  margin-top: 5px;
  background-color: rgba(122, 122, 122, 0.925);
  border-radius: 13px;
  /* (height of inner div) / 2 + padding */
  padding: 3px;
}
.progressbar > div {
  background-color: #2596be;
  /*width: 45%;*/
  /* Adjust with JavaScript */
  height: 20px;
  border-radius: 10px;
}

